import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/pageA.vue'
import Solo from '../views/soloPage.vue'
import Groupe from '../views/groupePage.vue'

const routes = [
      {
            path: '/',
            name: 'Home',
            component: Solo
      },
      {
            path: '/solo',
            name: 'solo',
            component: Home
      },
      {
            path: '/groupe',
            name: 'groupe',
            component: Groupe
      },
]

const router = createRouter({
      history: createWebHistory(process.env.BASE_URL),
      routes
})

export default router