<template>
  <div class="soloPage">
    <div class="nav-select">
      <div class="selector-group">
        <div class="select-btn nameBtn">
          <select name="poeple" id="poeple-selector" v-model="nameSelector">
            <option value="Etienne">Etienne</option>
            <option value="Max">Max</option>
            <option value="Jordan">Jordan</option>
          </select>
        </div>
        <div class="select-btn dateBtn">
          <input type="week" name="week" id="camp-week" class="date-button" v-model="weekSelector">
        </div>
      </div>
    </div>
    <div v-if="nameSelector != '' && weekSelector != null && weekSelector != '' && scheduleFound === true" class="schedule-true">
      <div class="card-box" v-for="day in days" :key="day.id">
        <div class="card">
          <div class="days-date">
            <div v-if="windowWidth >= 700" class="day">{{day.name}}</div>
            <div v-else class="day">{{day.name.substring(0,3)}}.</div>
            <div class="date">{{ day.val[0].toString().padStart(2, "0")}}/{{ day.val[1].toString().padStart(2, "0")}}</div>
          </div>
          <div v-if="currentSch[day.name].length > 2" class="days-content">
            <div class="item_number">
              <div class="item">
                <div class="schedule-day">
                  {{currentSch[day.name][0]}}h - {{currentSch[day.name][1]}}h
                </div>
                <div class="break">
                  <h1>Pause :</h1>
                  <h2>{{currentSch[day.name][2]}} min</h2>
                </div>
              </div>
              <div v-if="currentSch[day.name].length > 3" class="coupure">
                <div class="ligne"></div>
                <div class="item">
                  <div class="schedule-day">
                    18h - 20h
                  </div>
                  <div class="break">
                    <h1>Pause :</h1>
                    <h2>16 min</h2>
                  </div>
                </div>
              </div>
                
            </div>
            
          </div>
          <div v-else class="repos">
            <div class="repos-text">
              Repos
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="erreur-msg">
      Aucun Planning Trouv√©
    </div>
    
  </div>  
</template>

<script>
import  Eti_sch  from '../assets/Etienne_schedule.json'
import  max_sch  from '../assets/max_schedule.json'
import  jor_sch  from '../assets/jordan_schedule.json'


export default {
  data() {
    return {
      days: [{name:'Lundi',val:[]},{name:'Mardi',val:[]},{name:'Mercredi',val:[]},{name:'Jeudi',val:[]},{name:'Vendredi',val:[]},{name:'Samedi',val:[]},{name:'Dimanche',val:[]}],
      people: [
        { name: 'Etienne', schedule: Eti_sch },
        { name: 'Max', schedule: max_sch },
        { name: 'Jordan', schedule: jor_sch}
      ],
      nameSelector: '',
      weekSelector: null,
      week:null,
      year:null,
      scheduleFound:false,
      currentSch:'',
      windowWidth: window.innerWidth,
    }
  },
  mounted() {
    window.addEventListener("resize", this.updateWindowWidth);
  },
  methods:{
       getDateOfISOWeek(w, y) {
        var simple = new Date(y, 0, 1 + (w - 1) * 7);
        var dow = simple.getDay();
        var ISOweekStart = simple;
        if (dow <= 4)
          ISOweekStart.setDate(simple.getDate() - simple.getDay() + 1);
        else
          ISOweekStart.setDate(simple.getDate() + 8 - simple.getDay());

        return ISOweekStart;
      },
      parseWeekYear(){
        const value = this.weekSelector.toString()
        const parts = value.split('-W')
        this.week = parseInt(parts[1]);
        this.year = parseInt(parts[0]);
      },
      scheduleFoundVerif(name,week){
        console.log(name, this.people[2].name)
        var pers = this.people.filter(personne => personne.name === name);
        if(pers.length != 0){
          var scheduleWeek = pers[0].schedule[week]
          if(scheduleWeek){
            console.log(scheduleWeek)
          this.currentSch = scheduleWeek
          this.scheduleFound = true
          }else{
            this.scheduleFound = false
          this.currentSch = ''
          }          
        }
      },
      updateWindowWidth() {
      this.windowWidth = window.innerWidth;
    }
  },
  watch: {
    weekSelector() {
      this.parseWeekYear()
      this.scheduleFoundVerif(this.nameSelector,this.weekSelector)

      let start_date = this.getDateOfISOWeek(this.week,this.year);
      for (let i = 0; i < 7; i++) {
          let current_day = new Date(start_date.getTime());
          current_day.setDate(current_day.getDate() + i);
          this.days[i].val[0] = current_day.getDate();
          this.days[i].val[1] = current_day.getMonth() + 1;
      }
    },
    nameSelector(){
      this.scheduleFoundVerif(this.nameSelector,this.weekSelector)
    }
  },
}


</script>

<style>
@import url('https://fonts.googleapis.com/css2?family=Anton&family=Carter+One&family=Poppins:wght@500&display=swap');

  .box{
    width: 100vw;
  }
  .text-style{
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial;
  }
  .card-box{
      display: flex;
      align-items: center;
      width: 100vw;
      flex-direction: column;
  }
  .card{
    width: 60vw;
    height: 200px;
    margin-bottom: 30px;
    border-radius: 15px;
    display: flex;    
    box-shadow: 0px 0px 30px black;
  }
  .days-date{
    flex: 0 0 30%;
    max-width: 30%;
    background: #11101b;
    border-radius:15px 0 0 15px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    line-height:50px;
    
  }
  .days-date .day{
    font-family: 'Anton', sans-serif;
    font-size: 50px;
    color: white;    
  }
  .days-date .date{
    font-family: 'Anton', sans-serif;
    font-size: 30px;
    color: white;
  }

  .days-content{
    flex: 0 0 70%;
    max-width: 70%;
    background: white;
    border-radius:0 15px 15px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .item_number{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .coupure{
    display: flex;
    width: 100%;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  .repos{
    flex: 0 0 70%;
    max-width: 70%;
    background: rgb(167, 167, 167);
    border-radius:0 15px 15px 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }

  .item, .repos-text{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .schedule-day, .repos-text{
    flex: 0 0 80%;
    max-width: 80%;
    font-size: 35px;
    font-family: "Anton", sans-serif;    
    text-shadow: 4px 4px 20px rgb(56, 56, 56);
  }
  .break{
    flex: 0 0 20%;
    max-width: 20%;    
    text-shadow: 4px 4px 20px rgb(56, 56, 56);
  }

  .break h1{
    font-size: 20px;    
    text-shadow: 4px 4px 20px rgb(56, 56, 56);
  }
  .break h2{
    font-size: 15px;
    text-shadow: 4px 4px 20px rgb(56, 56, 56);
  }
  .ligne{
    height: 1px;
    width: 320px;
    margin: 15px 0 15px 0;
    border: 1px solid black;
    box-shadow: 0 0 10px black;
  }
  .erreur-msg{
    top: 50vh;
    text-align: center;    
    font-size: 30px;
    font-family: "Anton", sans-serif;    
    text-shadow: 4px 4px 20px rgb(56, 56, 56);
  }

  @media only screen and (max-width: 1150px) {
    .card{
    width: 80vw;
    }

  }

  @media only screen and (max-width: 870px) {
    .days-date .day{
    font-size: 45px;   
    }

  }

  @media only screen and (max-width: 760px) {
    .card{
    height: 150px;
    }

    .days-date .day{
    font-size: 40px;   
    }
    .days-date .date{
      font-size: 20px;
    }
    .days-date{
      line-height:35px;
    }
    .break h1{
    font-size: 18px;
    }
    .break h2{
    font-size: 13px;
    }

  }

  @media only screen and(max-width: 700px){
    .days-date .day{
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  
    
  }

  @media only screen and (max-width: 570px) {
    .ligne{
      width: 250px;
    }
  }

  @media only screen and (max-width: 515px) {
    .ligne{
      width: 150px;
      margin: 7px 0 7px 0;
    }

    
    .break h1{
    font-size: 14px;
    }
    .break h2{
    font-size: 12px;
    }
    .schedule-day{
      font-size: 28px;
    }

    .card{
      height: 135px;
    }
  }

  @media only screen and (max-width: 415px) {
    
    .break h1{
    font-size: 12px;
    }
    .break h2{
    font-size: 9px;
    }
    .schedule-day{
      font-size: 26px;
    }

    .card{
      height: 110px;
      margin-bottom: 20px;
    }

  }


</style>

   